@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&family=Yusei+Magic&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Oswald', sans-serif;
  font-weight: 600;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Main_wrapper{
    margin-bottom: 50px;
}
.content_wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 95%;
    height: 100%;
    margin: 0 auto;
    margin-top: 15px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.source-publish{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
    position: relative;
}
.source-publish p{
    font-size: 17px;
    text-transform: uppercase;
    font-family: 'Yusei Magic', sans-serif;
}
.bold{
    font-weight: 800;
    color: rgb(138, 50, 50);
}
.halka{
    font-weight: 700;
    color: brown;
    background-color: rgb(56, 51, 51);
    padding: 8px;
    color: white;
    border-radius: 5px;
}
.publish{
    background-color: rgba(0,0,0,0.7);
    color: white;
    padding: 5px 7px  5px 7px;
    border-radius: 3px;
    margin-right: 15px;
    margin-left: -5px;
}
.source{
    color: rgb(60, 110, 60);
    padding-right: 15px;
    font-size: 20px;
}
.image-url{
    width: 350px;
    height: 340px;
}
.image-url img{
    width: 100%;
    height: 100%;
}
.title-text{
    font-size: 20px;
    font-weight: 600;
    padding-left: 10px;
}
.gif{
    text-align: center;
}
.contact{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    margin-top: 10px;
    background-color: cornsilk;
}
.contact img{
    border-radius: 50%;
    box-shadow: 5px 7px 8px 0 rgba(0, 0, 0, 0.6);
}
.header{
    background-color: rgb(228, 214, 27);
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 45px;
}
.header_content{
    position: absolute;
    top: 5px;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 35px;
    background-color: white;
}
.man_logo{
    position: absolute;
    left: 100px;
    background-color: rgb(71, 69, 69);
    border-radius: 20%;
    font-size: 18px;
    padding: 0 3px 0 3px;
}
.logo{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 10px 0 5px;
    text-transform: uppercase;
    border-right: 1px solid gray;
    pointer-events: none;
}
.logo p{
    background-color: black;
    font-size: 16px;
    color: white;
    margin: 2px;
    padding: 0 4px 0 4px;
    font-weight: 700;
    border: none;
    outline: none;
}
.nav-links{
    margin-right: 30px;
}
.nav-links ul{
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-transform: uppercase
}

.nav-links ul li a{
    color: black;
    font-size: 13px;
    font-weight: 700;
    text-decoration: none;
    padding: 5px;
    border-right: 1px solid gray;
    cursor:pointer;
    opacity: 0.7;
}
.nav-links ul li a:hover{
    border-bottom: 3px solid green;
    opacity: 1;
}
footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 90px;
    background-color: rgb(43, 42, 42);
    color: white;
    pointer-events: none;
    font-family: 'Yusei Magic', sans-serif;
}
.footer-p{
    display: flex;
    align-items: center;
    justify-content: center;
}
.dev{
    background-color: rgb(250, 249, 249);
    color: rgb(36, 21, 36);
    font-weight: 800;
    margin-left: 8px;
    margin-right: 5px;
    padding: 0 4px 0 4px;
    border-radius: 3px;
}
.footer-p p{
    font-size: 15px;
}
.copyright p{
    bottom: 0;
    border-top: 1px dotted rgb(165, 164, 164);
    border-bottom: 1px dotted rgb(163, 163, 163);
    font-size: 14px;
    padding: 0 0 2px 0;
}
